<template>
    <div class="swiper_box">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(items, index) in imgList" :key="index">
                    <img v-lazy="items.url">
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>

           <!-- &lt;!&ndash; 如果需要导航按钮 &ndash;&gt;
            <div class="swiper-button-prev"></div>
            <div class="swiper-button-next"></div>-->

            <!-- 如果需要滚动条 -->
            <!--<div class="swiper-scrollbar"></div>-->
        </div>
    </div>
</template>

<script>
    import Swiper from 'swiper'
    import img1 from '../assets/images/timg1.jpg'
    import img2 from '../assets/images/timg2.jpg'
    import img3 from '../assets/images/timg3.jpg'
    import img4 from '../assets/images/timg4.jpg'
    export default{
        components:{
        },
        data(){
            return{
                imgList:[
                    {id:'1', url:img1},
                    {id:'2', url:img2},
                    {id:'3', url:img3},
                    {id:'4', url:img4}
                ]
            }
        },
        mounted() {
            let swiper = new Swiper('.swiper-container',{
                init:false,
                speed:600,
                loop:true,
                autoplay: {
                    delay:3000
                },
                // 如果需要分页器
                pagination: {
                    el: '.swiper-pagination',
                    clickable :true,
                },

             /*   // 如果需要前进后退按钮
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },*/

                // 如果需要滚动条
                /*scrollbar: {
                    el: '.swiper-scrollbar',
                },*/
            });

            swiper.init();
        }
    }
</script>
<style lang="scss">
    $prefix: swiper_box;

    .#{$prefix}{
        width: 100%;
        height: 100%;
        .swiper-container{
            width: 100%;
            height: 100%;
            background-color: antiquewhite;
            display: flex;
            align-items: center;
            .swiper-slide{
                img{
                    width: 100%;
                }
            }
        }
    }
</style>