import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        nav: "Home",
        show: true,
        isActive: '1',
        type:0
      }
    },
    {
      path: '/art',
      name: 'Art',
      component: () => {
        return import('./views/article.vue')
      },
      meta: {
        nav: "Article",
        show: true,
        isActive:'2',
        type:1
      }
    },
    {
      path: '/artDetail',
      name: 'ArtDetail',
      component: () => {
        return import('./views/article_detail.vue')
      },
      meta: {
        nav: "详情",
        show: true,
        isActive:'2',
        type:2,
        parent:'art',
        parentName:'Article'
      }
    },
    {
      path: '/aboutMe',
      name: 'AboutMe',
      component: () => {
        return import(/* webpackChunkName: "about" */ './views/about_me.vue')
      },
      meta: {
        nav: "AboutMe",
        show: true,
        isActive:'6',
        type:1
      }
    }
  ]
})
